import axios from "axios"
import {  HOME_SET_GOODS, DETAIL_SET_GOODS,SET_TYPE_LIST ,SET_CAR} from "./actionTypes";
import {http} from "../src/utils";

//推荐页面数据
export let setGoods = list => ({
    type: HOME_SET_GOODS,
    list
})

export let getGoods = () => {
    
    return function (dispatch) {
        axios.get("https://webservice.juanpi.com/api/getGoods?page=2&zy_ids=p8_c4_l4&app_name=zhe&catname=newest_zhe")
            .then(({ data }) => {
                dispatch(setGoods(data.data.goods))
            })
    }
}

//详情页面数据
export let setDetail=DetailList=>({
    type: DETAIL_SET_GOODS,
    DetailList
})
export let getDetail = goodsID=>dispatch => {
    axios.get(`https://webservice.juanpi.com/api/getMemberAboutInfo?goods_id=${goodsID}`)
        .then(({ data }) => {
        dispatch(setDetail(data.skudata))
    })
}

//分类
export let settypelist=typelist=>({
    type: SET_TYPE_LIST,
    typelist
})

export let gettypelist=()=>dispatch=>{
    http.get("/yd/typelist")
    .then((data)=>{
        dispatch(settypelist(data.data))
    })
    
}

export let setcar=(cargoods,checkArr)=>({
    type:SET_CAR,
    cargoods,
    checkArr
})
export let getcar=(tel)=>dispatch=>{
    console.log(tel)
    http.get("/yd/getcar",{params:{tel:tel}})
        .then((data)=>{
            
            let checkArr=data.data.map((item,i)=>{
                item.checked=false;
                return item.checked
            });
            console.log(data.data,checkArr)
            dispatch(setcar(data.data,checkArr))
        })
}


