import { connect } from "react-redux"
import { getDetail } from "../action";
import React from "react"
import { Toast } from 'antd-mobile';
import "./detail.scss";
import axios from "axios"
import {http} from "../utils";
class UI extends React.Component {
    // constructor(props){
    //     super(props)
    // }

    componentDidMount() {
        this.props.init()
    }
    state={
        flag:true,
        num:0,
        num2:0,
        inpNum:1
    }
    quxiao(){
        this.setState({flag:true})
    }
    jionbtn(){
        this.setState({flag:false})
    }
    addclass(i){
        this.setState({num:i})
        let val=document.getElementsByClassName("active")[0].innerHTML
        let select=document.getElementById("select")
        select.innerHTML='已选择: '+ val
    }
    addclass2(i){
        this.setState({num2:i})
    }
    addinpnum(){
        this.setState({inpNum:++this.state.inpNum})
        console.log(this.state.inpNum)
    }
    handleGetInputValue=(event)=>{  
        this.setState({inpNum:event.target.value})  
    }
    intocar(){
        console.log(0)
        let tel=localStorage.usertel;
        let title=document.getElementById("title").textContent;
        let goodid=this.props.match.params.gid;
        let inpNum=this.state.inpNum;
        let img=document.getElementById("img").src
        let color=document.getElementsByClassName("active")[0].innerHTML||"";
        let size=document.getElementsByClassName("actived")[0].firstChild.innerHTML||'';
        let price=document.getElementById("price").innerHTML.slice(1);
        let oprice=document.getElementsByClassName("oprice")[0].innerHTML.slice(5)
        console.log(color,size,price,tel,goodid,inpNum,title,img)
        if(tel){
            
            http.get("/yd/intocar",{params:{
                tel:tel,
                title:title,
                goodid:goodid,
                inpNum:inpNum,
                color:color,
                size:size,
                price:price,
                img:img,
                oprice:oprice,
                checked:false
            }})
            .then(data=>{
                console.log(data)
                if(data.data.result.code==1){
                    Toast.info(data.data.result.msg)
                    this.setState({flag:true})
                }else{
                    Toast.info(data.data.result.msg)
                }
            })
        }else{
            this.props.history.push("/login")
        }
       
    }

    render() {
        
       
        let { goods ,history} = this.props
        const hide={
            display:this.state.flag?"none":"block"
        }
        let dom = null
        
        if (goods) {
            let {
                goods_title, oprice, cprice, goods_origin_url, join_number,goods_pic_url,fav_name
            } = goods.info
            let itemarr=goods.sku
            let av_zvalue =[];
            itemarr.map(function(item,i){
                if(av_zvalue.indexOf(item.av_zvalue)==-1){
                    av_zvalue.push(item.av_zvalue)
                    
                }
                return av_zvalue
            })
            let color=av_zvalue.map((item,i)=>(
                    <span key={i} 
                        className={this.state.num==i?"active":""} 
                        style={{display:item?"block":"none"}} 
                        onClick={()=>this.addclass(i)}>{item}
                    </span>
            ))
            
            let list=itemarr.map((item,i)=>(
                <li key={i} 
                    style={{display:item.av_fvalue?"block":"none",background:item.stock_tips=="抢光了"?"rgb(240, 240, 240)":""}} 
                    className={this.state.num2==i?"actived":""}
                    onClick={item.stock_tips=="抢光了"?()=>{Toast.info("该商品已经抢光了")}:()=>this.addclass2(i)}>
                    <p >{item.av_fvalue}</p>
                    <span>{item.stock_tips}</span>
                </li>
            ))   
                
            
            
            
            dom = (
                <div className='goodshow'>
                    <i className="iconfont icon-xiangzuojiantou" onClick={()=>history.go(-1)}></i>
                    <img src={goods_origin_url} id="img" />
                    <div className='box'>
                        <span>价格:<i id="price">￥{cprice}</i></span>
                        <span className='oprice'>原价: ￥{oprice}</span>
                        <span className="email">包邮</span>
                        <span className='join_number'>{join_number}</span>
                    </div>
                    <div className='title'>
                        <h3 className='goods_title ellipsis2' id="title">{goods_title}<i className="iconfont icon-jushoucang"></i></h3>
                        <ul className="clr">
                            <li>24小时发货</li>
                            <li>7天包退</li>
                            <li>售后补贴</li>
                            <li className='flt'>···</li>
                        </ul>
                    </div>
                    <div className='comment'>
                        <h3>用户评论(<i>0</i>)</h3>
                    </div>
                    <div className='intogwc'>
                        <span className='kefu'><i className='iconfont icon-xiaoxi'></i>客服</span>
                        <span className='gwc'><i className='iconfont icon-gouwuche'></i>购物车</span>
                        <span className='buy'>立即购买</span>
                        <span className='jion' onClick={()=>this.jionbtn()}>加入购物车</span>
                    </div>
                    <div className="addcar-box" style={hide}>
                        <div className='addcar'>
                            <img src={goods_pic_url} alt='' className="smallimg"/>
            
                            <p className="price">￥{cprice}<span id="select">已选择: {itemarr[0].av_zvalue}</span></p>
                            <h3>类型</h3>
                            <p className="color" id="color">{color}</p>
                            <h3>{fav_name}</h3>
                            <ul>
                                {list}
                            </ul>
                            <div className="buynum">
                                <span>购买数量</span> 
                                <p>
                                    <i onClick={()=>{this.state.inpNum>1?this.setState({inpNum:--this.state.inpNum}):this.setState({inpNum:1})}}  >-</i>
                                    <input type="number" value={this.state.inpNum}  id="inpNum" onChange={this.handleGetInputValue} />
                                    <i onClick={()=>{this.addinpnum()}}>+</i>
                                </p>
                            </div>
                            <span className="okbtn" onClick={()=>{this.intocar()}}>确定</span>
                            <i className="colsebtn" onClick={()=>this.quxiao()} >x</i>
                        </div>
                    </div>
                </div>
            )
        }

        return (
            <div>
                {dom}
            </div>
        )
    }
}
let mstp = state => {
    return {
        goods: state.detail.goods
    }
}
let mdtp = (dispatch, props) => {
    return {
        init() {
            let goodsID = props.match.params.gid
            dispatch(getDetail(goodsID))
        }
    }
}
export let Detail = connect(mstp, mdtp)(UI)