import { NavLink } from "react-router-dom";
import React from "react";


export class Foot extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            list: [
                { path: "/home", text: "首页", Iclass: "iconfont icon-shouye1" },
                { path: "/shop", text: "品牌清仓", Iclass: "iconfont icon-pinpaiqingcang" },
                { path: "/shopingcar", text: "购物车", Iclass: "iconfont icon-ai-cart" },
                { path: "/my", text: "我的卷皮", Iclass: "iconfont icon-geren2" }
            ]
        }
    }

    render() {
        let {history}=this.props
        let domList = this.state.list.map(({ path, text, Iclass }) => <NavLink activeClassName="active" className="nav-a" to={path} key={path}>
            <p className={Iclass}></p>

            <p>{text}</p>
        </NavLink>)
        return (
            <div className="foot-box">
                <footer className="clr">
                    {domList}
                </footer>
             </div>

        )
    }
}