
import React from "react";
import "swiper/dist/css/swiper.css"
import Swiper from "swiper"
import {Link} from "react-router-dom";


export class Imgshow extends React.Component {
  

  componentDidMount() {
    this.swiper = new Swiper(".swiper-container", {
      autoplay: true
    })
  }
  componentDidUpdate(){
    this.swiper.update()
  }

 

  render() {
    return (
      <div className="swiper-container">
        <div className="swiper-wrapper">
          <div className="swiper-slide"><Link to="home"><img src='https://goods8.juancdn.com/jas/190301/f/5/5c78cd28b6f8ea45c172f522_1080x418.jpg' alt='' /></Link></div>
          <div className="swiper-slide"><Link to="home"><img src='https://goods4.juancdn.com/jas/190301/7/2/5c78944433b089206921fc12_1080x418.jpg'  alt=''/></Link></div>
          <div className="swiper-slide"><Link to="home"><img src='https://goods4.juancdn.com/jas/190301/7/0/5c78cd9bb6f8ea2aba144418_1080x418.jpg'  alt=''/></Link></div>
          <div className="swiper-slide"><Link to="home"><img src='https://goods7.juancdn.com/jas/190228/d/c/5c77851bb6f8ea6fed37b206_1080x418.jpg'  alt=''/></Link></div>
          <div className="swiper-slide"><Link to="home"><img src='https://goods7.juancdn.com/jas/190301/c/d/5c78d12733b08921b666cb24_1080x418.jpg'  alt=''/></Link></div>
        </div>
      </div>
    )
  }
}


