import React from "react";
import "./tel-register.scss";
import axios from "axios"
import { Toast } from 'antd-mobile';
import {http} from "../utils";

export class TelRegister extends React.Component{
    text(){
        let phone = document.getElementById('phone').value;
        if(!(/^1[34578]\d{9}$/.test(phone))){ 
            
            this.setState(prevState => ({
                flag:true
              }))
             
        } else{
            this.setState(prevState=>({
                flag:false
              }))
        }
    }
    
    
    componentDidMount() {
        setTimeout(() => {
          Toast.hide();
        }, 2000);
      }


    constructor(props){
        super(props)
        this.state={
            flag:true,
            check:false,
        i:60,
        msg:"获取验证码",
        }
    }
    getyzm(){
        let time =  setInterval(()=>{
            this.state.i--;
        this.setState({
            msg:`${this.state.i}s`,
            flag:true
        })
        if(this.state.i<=0){
            this.setState({
                msg:"获取验证码",
                flag:false
            })
            clearInterval(time) 
        }
        },1000)

        let tel=document.getElementById("phone");
        http.get("/yd/msg" ,{params:{tel:tel.value}})
            .then((data)=>{
                console.log(data.data.code)
                this.setState({code:data.data.code})
            })
    }
    ang(e){
        console.log(e.target.checked)
        if(e.target.checked){
            this.setState({check:true})
        }else{
            this.setState({check:false})
        }
    }
    successToast(){
        let tel=document.getElementById("phone");
        let phone = document.getElementById('phone').value;
        let yzm=document.getElementById("yzm");
        let yzmval=yzm.value
        console.log(this.state.code)
        if((/^1[34578]\d{9}$/.test(phone))){
           
                if(yzmval==this.state.code){
                    
                    axios.get(":4200/yd/zhuce",{params:{usertel:phone}}).then((data)=>{
                        if(data.data.result.code){
                            this.props.history.push("/mymsg/"+phone)
                        }else{
                            Toast.info(data.data.result.msg)
                        }
                    })
                }else{
                    Toast.info("验证码不正确")
                }
            
        }else{
            Toast.info("手机号不合法")
        }
        
    }
   
    render(){
        console.log(this.state.msg)
        const customIcon = () => (
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 64 64" className="am-icon am-icon-md">
              <path fillRule="evenodd" d="M59.177 29.5s-1.25 0-1.25 2.5c0 14.47-11.786 26.244-26.253 26.244C17.206 58.244 5.417 46.47 5.417 32c0-13.837 11.414-25.29 25.005-26.26v6.252c0 .622-.318 1.635.198 1.985a1.88 1.88 0 0 0 1.75.19l21.37-8.545c.837-.334 1.687-1.133 1.687-2.384C55.425 1.99 53.944 2 53.044 2h-21.37C15.134 2 1.667 15.46 1.667 32c0 16.543 13.467 30 30.007 30 16.538 0 29.918-13.458 29.993-30 .01-2.5-1.24-2.5-1.24-2.5h-1.25" />
            </svg>
          );
          
       
        let {history} = this.props;
        const stylecolor={
            color:this.state.flag?'':'blue'
        }
        const backcolor={
            background:this.state.flag1?"":"skyblue"
        }
        
        return (
        <div className='tel-register'>
            <div className="head clr">
                <p className="title frt">已有账号?登陆</p>
                <i className="iconfont icon-xiangzuojiantou" onClick={()=>history.go(-1)}></i>
            </div>
            <div className="tel-content">
                <h3>手机号注册
                    <p>请确保你的手机通畅，用于接收验证码短信</p>
                </h3>
                <p>
                    <input placeholder='请输入手机号码' id='phone' className='phone' onBlur={()=>(this.text())}/>
                </p>
                <p>
                    <input placeholder='请输入验证码' className='yzm'  id="yzm" />
                    <button disabled={this.state.flag?true:false} onClick={()=>this.getyzm()}  style={stylecolor}>{this.state.msg}</button>
                </p>
                <span className='next' onClick={()=>(this.successToast())} style={backcolor}    >下一步</span>
            </div>
            {/* <p className="xy"><input type='checkbox' id="check"  checked={this.state.check||false} onChange={(e)=>this.ang(e)} />已阅读并同意服务协议及隐私政策</p> */}
            
        </div>
        
        )
    }
}



