import React from "react"
import "./mymsg.scss"
// import {Date} from "../components/my/riqi"
import {Toast} from "antd-mobile"
import axiso from "axios"
import {http} from "../utils";
export class Mymsg extends React.Component{
    
    upmsg(){
        var num=0;
        let tel=this.props.history.location.pathname.split("/")[2];
        let nickname=document.getElementById("nickname").value;
        let pwd=document.getElementById("pwd").value;
        let pwd2=document.getElementById("pwd2").value;
        let val=document.getElementsByName("sex");
        var selectvalue=null;   //  selectvalue为radio中选中的值
        for(let i=0;i<val.length;i++){
               if(val[i].checked==true) {
                       selectvalue=val[i].value;
               }
        }
        
        if(nickname){
            num++
        }else{
            Toast.info("请输入用户名")
        }
        if(/[0-9]{4}$/.test(pwd)){
            num++
        }else{
            Toast.info("密码格式不正确")
        }
        if(pwd==pwd2){
            num++
        }else{
            Toast.info("密码不一致")
        }
        if(num==3){
            console.log(0)
           http.get(":4200/yd/upmsg" ,{params:{tel:tel,pwd:pwd,nickname:nickname}}).then(data=>{
               console.log(data)
               if(data.data.result.code==1){
                    Toast.info(data.data.result.msg)
                    this.props.history.push("/my")
               }else{
                Toast.info(data.data.result.msg)
               }
           })
        }
    }
    render(){
        let {history}=this.props
        return (
            <div className="mymsg-page">
                <h3><i className="iconfont icon-shouye" onClick={()=>this.props.history.push("/home")}></i>完善个人信息</h3>
                <p><span>昵称 :</span><input placeholder='请输入您的昵称'  id="nickname" /> </p>
                <p><span>密码 :</span><input placeholder='设置密码' id="pwd"  type="password"/> </p>
                <p><span>确认密码 :</span><input placeholder='确认密码' id="pwd2" type="password" /> </p>
                <p className="sex-box">
                     <label htmlFor="sexnan">男 :</label>
                        <input type='radio' name="sex" id="sexnan" defaultChecked  value='男'/>
                    <label htmlFor="sexnv" value="女">女 :</label>
                        <input type='radio' name="sex" id="sexnv" />        
                </p>
                {/* <p><span>出生年月</span><Date/></p> */}
                <span className="tijiao" onClick={()=>this.upmsg()}>提交</span>
            </div>
        )
    }
}

