import React from "react";
import {Badge} from "antd-mobile"
import  {Link} from "react-router-dom"


export class Login extends React.Component {


    render() {
        // let {tel}=this.props;
        let Islogin=null;
        let tel=localStorage.usertel
        let nickname=localStorage.nickname
        console.log(tel)
        if(tel){
             Islogin=(
                <div className="pic-box">
                    <div className='pic'></div>
                    <span>欢迎您: {nickname}</span>
                </div>
            )
        }else{
             Islogin=(
                <div className='login'>
                    <Link className="lg" to='/login'>登陆</Link>
                    <Link to='/register'>注册</Link>
                    <Badge text="送28元" hot className="hot" />
                </div>
            )
        }
        return (
            <div>
                {Islogin}
            </div>
        )
    }
}

