import React from "react";
import { HashRouter as Router, Route, Switch, Redirect } from "react-router-dom";



import { Home } from "./home";
import { My } from "./my";
import { Shop } from "./shop";
import { Shopingcar } from "./shopingcar";

import {Provider} from "react-redux"
import { store } from "../store";
import { Detail } from "./detail";
import { Register } from "./register";
import { TelRegister } from "./tel-register";
import { Type } from "./type";
import { Mymsg } from "./mymsg";
import { Typegoods } from "./type-goods";
import { LoginPage } from "./login";
export class App extends React.Component {
    render() {
        return (
            <div className="page-box clr">
                <Provider store={store}>
                <Router>
                    <div className="router-box clr">
                        <Switch>
                            <Redirect from="/" exact={true} to="/home" />
                            <Route path="/home" component={Home} />
                            <Route path="/my" component={My} />
                            <Route path="/shop" component={Shop} />
                            <Route path="/shopingcar" component={Shopingcar} />
                            <Route path="/detail/:gid" component={Detail} />
                            <Route path="/register" component={Register} />
                            <Route path="/tel-register" component={TelRegister} />
                            <Route path="/type" component={Type} />
                            <Route path="/mymsg" component={Mymsg} />
                            <Route path="/typegoods" component={Typegoods} />
                            <Route path="/login" component={LoginPage} />
                        </Switch>
                    </div>
                </Router>
                </Provider>
            </div>
        )
    }
}

