import { SET_CAR } from "../actionTypes";

export let car = (state={
    goods : null,
    check:[]
},action) => {
    console.log(1)
    let {type} = action
    let newState = {...state}
    switch (type) {
        case SET_CAR:
            newState.goods = action.cargoods
            newState.check = action.checkArr
            return newState
        default:
            return state
            break;
    }
}