import "./List.scss"
import {Link} from "react-router-dom"
import React from "react"

export class Goods extends React.Component{
    
    render(){
        let domList=this.props.list.map(
            ({goods_id,title,oprice,cprice,pic_url})=>(
                <li key={goods_id} className='flt'>
                <Link to={`/detail/${goods_id}`}>
                <div className="img-box">
                    <img src={pic_url} alt='' />
                </div>
                <h3>{title}</h3>
                <div className="price">
                    <p className="flt">现价：<span>{cprice}</span></p>
                    <p className="frt"><del>原价：{oprice}</del></p>
                </div>
                
                </Link>
                </li>
            )
        )
        return(
            <ul className="goods-list clr">
                {domList}
                <br></br>
            </ul>
        )
    }
}