import { SET_TYPE_LIST } from "../../../actionTypes";

export let type = (state={
    typelist : [],
  
},action) => {
    let {type} = action
    let newState = {...state}
    switch (type) {
        case SET_TYPE_LIST:
            newState.typelist = action.typelist
            return newState
        default:
            return state
            break;
    }
}