import { connect } from "react-redux"
// import { getDetail } from "../action";
import React, { Component } from "react"
import "./type.scss";
import { SearchBarExample } from "../components/home/type/type-nav"
import { TypeMain } from "../components/home/type/type-main"

import { store } from "../store";
class UI extends React.Component {
    render() {
        return (
            <div className="type-body">
                <SearchBarExample />
                <TypeMain />
            </div>
        )
    }
}


export class Type extends React.Component {
    constructor(props) {
        super(props)
        this.state = store.getState()
    }
    render() {
        return <UI {...this.state} />
    }
}
