import { HOME_SET_GOODS } from "../../actionTypes";

export let home = (state={
    goodsList : [],
  
},action) => {
    let {type} = action
    let newState = {...state}
    switch (type) {
        case HOME_SET_GOODS:
            newState.goodsList = action.list
            return newState
        default:
            return state
            break;
    }
}