import { connect } from "react-redux"
import { gettypelist } from "../../../action";
// import { gettypegoods } from "../../../action";
import React, { Component } from "react"
import { Link } from "react-router-dom"

import "./type-main.scss"

export class UI extends React.Component {
    componentDidMount() {
        this.props.init()

    }

    state = {
        i: 0,
       
        
    };

    addClass = i => {
        this.setState({i})
        let odiv=document.getElementsByClassName("type-box")
        let odivs=Array.from(odiv)
        odivs.map((item,i)=>{
            item.className="type-box"
        })
        odivs[i].className="active type-box"
       
    };
    



    render() {
        let { typelist ,history} = this.props
        let goods = typelist.map((item, i) => {
            return item.secondCateList
        })
        console.log(typelist)
        console.log(goods[this.state.i])
        if (goods[this.state.i]) {
            
            this.state.show = (goods[this.state.i]).map((item, j) => (
                
                    <a key={j}>
                        <img src={`https://s2.juancdn.com${item.icon}`} alt='' />
                        <p>{item.name}</p>
                    </a>               
            ))
        }
        
        let list = typelist.map((item, i) => (
            <div key={i} className='type-box clr' onClick={() => this.addClass(i)} num={i} >
                <span key={i} className="ospan">{item.name}</span>
            </div>
        ))            

        return (<div className="type-main">

            {list}
            <div className="clr goodslist">
            {this.state.show}
            </div>
            
        </div>);
    }
}


let mapStateToProps = state => {
    return {
        typelist: state.type.typelist,
        
    }
}
let mapDispathToProps = dispatch => {
    return {
        init() {
            dispatch(gettypelist())
        }
    }
}

export let TypeMain = connect(mapStateToProps, mapDispathToProps)(UI)
