import React from "react"
import { NavLink } from "react-router-dom";

export class HomeNav extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            goodType: [
                { path: "/home", text: "推荐" },
                { path: "/home", text: "女装" },
                { path: "/home", text: "居家" },
                { path: "/home", text: "鞋包" },
                { path: "/home", text: "母婴" },
                { path: "/home", text: "数码" },
                { path: "/home", text: "美妆" },
                { path: "/home", text: "美食" },
                { path: "/home", text: "男装" },
                { path: "/home", text: "内衣" },
                { path: "/home", text: "运动" },
                { path: "/home", text: "淘宝爆款" }
            ]
        }
    }
    render() {
        let goodType = this.state.goodType.map(({ path, text }) => <NavLink activeClassName="select" className="type" to={path} key={text}>{text}</NavLink>)
        return (
            <div className="home-box">
                <div className="clr goodType">
                    <div className="type-box">
                        {goodType}
                    </div>
                </div>
                <div className="home-menu iconfont icon-xiangxiajiantou">
                    {/* <i className="iconfont icon-xiangxiajiantou"></i> */}
                </div>
            </div>

        )
    }
}

