import React from "react";
import { Foot } from "../components/foot";
import { connect } from "react-redux"
import "../components/home/home.scss";
import "./shopingcar.scss"
import { getcar } from "../action"
import {http} from "../utils";


class UI extends React.Component {
    constructor(props) {
        super(props)

    }
    componentDidMount() {
        this.props.init()
        
    }
    state = {
        checkone:false,
        total:0,
    }
   
    checkall() {
        this.state.total=0
        console.log(this.props.goods)

        let checkall = document.getElementsByClassName("checkall")[0];
        let checkone = document.getElementsByClassName("checkone");
        let checkitem = Array.from(checkone);
        if (checkall.checked) {
            checkitem.map((item, i) => {
                return item.checked = true
            })
            
            this.props.goods.map((item,i)=>{
                this.state.total+=item.price*item.inpNum
               
            })
            this.setState({total:this.state.total})
        } else {
            checkitem.map((item, i) => {
                return item.checked = false
            })
            this.setState({total:0})
        }

    }
    checkone() {
        this.state.total=0
        let checkall = document.getElementsByClassName("checkall")[0];
        let checkone = document.getElementsByClassName("checkone");
        let checkitem = Array.from(checkone);
        if(checkitem.every((item, i) => {
            return item.checked
        })){
            checkall.checked=true;
        }else{
            checkall.checked=false;
        }
        
        checkitem.map((item,i)=>{
            console.log(this.state.total)
            if(item.checked){
             this.state.total+=this.props.goods[i].price*this.props.goods[i].inpNum;
            }
            this.setState({total:this.state.total.toFixed(1)})
            console.log(this.state.total)
        })
    }
    del(goodid){
        let tel=localStorage.usertel;
        http.get("/yd/del",{params:{tel:tel,goodid:goodid}}).then(data=>{
            console.log(data)
        })
    }


    render() {
        
        let { goods, list} = this.props;
        console.log(goods)
        if (goods) {
            if(goods.length==0){
              list=  (<div className="text">购物车空空如也,赶紧去购买吧！！！</div>)
            }else{
                list = goods.map((item, i) => (
                    <div className="main" key={i}>
                        <div className="box">
                            <input type="checkbox" className="checkone" onClick={() => { this.checkone() }} />
                        </div>
                        <div className="good-msg">
                            <img src={item.img} />
                            <p className="title">{item.title}
                                <i>{item.color}</i>
                                <span className="size">{item.size}</span>
                                <span>￥{item.price}
                                    <i className="oprice">￥{item.oprice}</i>
                                </span>
                            </p>
                        </div>
                        <p className="buynum"><i className="del" key={item._id} onClick={()=>{this.del(item.goodid)}}>删除</i> x{item.inpNum}</p>
                    </div>
                ))
            }
            
        }

        return (
            <div className="shopingcar-body">
                <div className="car-top">
                    购物车
                    <span className="frt">编辑</span>
                </div>
                <div className="content">
                    <div className="content-top">
                        <div className="zuo">
                            <input type="checkbox" className="checkall" onClick={() => { this.checkall() }} />
                            <span><i className="iconfont icon-duoyuyan"></i>跨店促销</span>
                        </div>
                        <div className="you">
                            <span>领卷</span>
                        </div>
                    </div>
                    {list}
                </div>

                <div className="pay-box">
                    <label htmlFor="checkall">
                        <input type="checkbox" id="checkall" /> 全选
                    </label>
                    <span className="total">合计<i>￥</i><i className="total-price">{this.state.total}</i></span>
                    <span className="pay">结算</span>
                </div>
                <Foot />
            </div>
        )
    }
}



let mstp = state => {
    return {
        goods: state.car.goods,
        check: state.car.check
    }
}
let mdtp = (dispatch, props) => {
    return {
        init() {
            let tel = localStorage.usertel
            dispatch(getcar(tel))
        }
    }
}
export let Shopingcar = connect(mstp, mdtp)(UI)