import { DETAIL_SET_GOODS } from "../../../actionTypes";

export let detail = (state={
    goods : null,
    title :"详情页"
},action) => {
    let {type} = action
    let newState = {...state}
    switch (type) {
        case DETAIL_SET_GOODS:
            newState.goods = action.DetailList
            return newState
        default:
            return state
            break;
    }
}