import {NavLink} from "react-router-dom";
import React from "react";


export class Banner extends React.Component{
    constructor(props){
        super(props)

        this.state = {
            bannerList : [
                {path:"/home",src:"https://goods4.juancdn.com/jas/181119/7/6/5bf2524bb6f8ea534d206f83_270x241.png?imageMogr2/quality/85!/format/png"},
                {path:"/home",src:"https://goods8.juancdn.com/jas/181228/f/e/5c25bd0d33b08962a220f3a6_270x241.png?imageMogr2/quality/85!/format/png"},
                {path:"/home",src:"https://goods2.juancdn.com/jas/180201/3/d/5a727415a9fcf8280d24465a_270x241.png?imageMogr2/quality/85!/format/png"},
                {path:"/home",src:"https://goods4.juancdn.com/jas/180917/6/5/5b9f175033b08945a870ad21_270x241.png?imageMogr2/quality/85!/format/png"}
            ],
            miaosha : [
                {path:"/home",src:"https://s2.juancdn.com/jas/190301/8/b/5c788b5db6f8ea476c3cc543_540x656.gif"},
                {path:"/home",src:"https://goods5.juancdn.com/jas/190301/9/4/5c78ebe833b08925f737deb6_540x328.png?iopcmd=convert&Q=85&dst=png"},
                {path:"/home",src:"https://goods1.juancdn.com/jas/190227/0/d/5c75e835b6f8ea6deb695124_540x328.png?iopcmd=convert&Q=85&dst=png"},   
            ]
        }
    }
    
    render(){
        let bannerList = this.state.bannerList.map(({path,src})=><NavLink className="banner-a" to={path} key={src}><img src={src} alt={path}/></NavLink>)
        let miaosha = this.state.miaosha.map(({path,src})=><NavLink className="miaosha" to={path} key={src}><img src={src} alt={path}/></NavLink>)
        return (
            <div className="banner">
                <div className="banner-box1">
                    {bannerList}
                </div>
                <div className="banner-box2 clr">
                    {miaosha}
                </div>  
            </div>
        )
    }
}